import React, { Fragment, useState, useEffect } from 'react';
import moment from 'moment';
import { Card, Input, Table, Button, Modal, Form, message, Popconfirm, Select } from 'antd';
import { PlusOutlined, DeleteOutlined } from '@ant-design/icons';
import { getTypeList, getModelList, addModel, deleteModel } from '../../api/api';

function ModelList() {
  const [visible, updateVisible] = useState(false);
  const [typeList, updateTypeList] = useState([]);
  const [modelList, updateModelList] = useState([]);
  const [form] = Form.useForm();

  const columns = [
    { title: '型号', dataIndex: 'name' },
    {
      title: '类别',
      key: 'type',
      render: (_, record) => {
        const current = typeList.find((type) => type.id === record.type_id);
        if (current) {
          return current.name;
        }
      },
    },
    // { title: '创建人', dataIndex: 'createBy' },
    {
      title: '创建时间',
      key: 'createTime',
      render: (_, record) => {
        if (record.create_time) {
          return moment(record.create_time).format('YYYY-MM-DD HH:mm:ss');
        }
      },
    },
    {
      title: '操作',
      key: 'action',
      width: '120px',
      render: (_, record) => (
        <Popconfirm title="确定删除吗?" onConfirm={() => deleteItem(record)} okText="确定" cancelText="取消">
          <a style={{ color: 'red', cursorJ: 'pointer' }}>
            <DeleteOutlined />
            删除
          </a>
        </Popconfirm>
      ),
    },
  ];

  useEffect(() => {
    getTypeList().then((res) => {
      if (res?.code === 200) {
        updateTypeList(res?.result);
      }
    });
    getList();
  }, []);

  const getList = (value) => {
    getModelList({ name: value }).then((res) => {
      if (res?.code === 200) {
        updateModelList(res?.result);
      }
    });
  };

  const deleteItem = async (item) => {
    const res = await deleteModel({ id: item.id });
    if (res?.code === 200) {
      message.success('删除成功');
      getList();
    } else {
      message.error('删除失败');
    }
  };

  const toggleVisible = () => {
    if (visible) {
      form.resetFields();
    }
    updateVisible(!visible);
  };

  const onSearch = (value) => {
    getList(value);
  };

  const handleOk = async () => {
    const values = await form.validateFields();
    const res = await addModel(values);
    if (res?.code === 200) {
      message.success('添加成功');
      getList();
      toggleVisible();
    } else {
      message.error('添加失败');
    }
  };

  return (
    <Fragment>
      <Card bordered={false}>
        <div style={{ marginBottom: 10 }}>
          <Input.Search placeholder="输入型号名称搜索" style={{ width: 300 }} allowClear onSearch={onSearch} />

          <Button type="primary" style={{ float: 'right' }} icon={<PlusOutlined />} onClick={toggleVisible}>
            添加
          </Button>
        </div>

        <Table dataSource={modelList} rowKey="id" columns={columns} pagination={{}} bordered size="small" />
      </Card>

      {visible && (
        <Modal title="添加型号" open cancelText="取消" okText="确定" onOk={handleOk} onCancel={toggleVisible}>
          <Form form={form} labelCol={{ span: 4 }} wrapperCol={{ span: 19 }} initialValues={{}} autoComplete="off">
            <Form.Item label="型号名称" name="name" rules={[{ required: true, message: '请填写型号名称' }]}>
              <Input />
            </Form.Item>
            <Form.Item label="类别" name="typeId" rules={[{ required: true, message: '请选择所属类别' }]}>
              <Select>
                {typeList.map((type) => (
                  <Select.Option value={type.id} key={type.id}>
                    {type.name}
                  </Select.Option>
                ))}
              </Select>
            </Form.Item>
          </Form>
        </Modal>
      )}
    </Fragment>
  );
}

export default ModelList;
